const key = 'user'
const user = {
  /*  user  */
  state() {
    return {
      user: null,
      // token: "",
    }
  },
  /*  getUser localStorage  user  JSON  */
  getters: {
    getUser: function (state) {
      if (!state.user) {
        state.user = JSON.parse(localStorage.getItem(key))
      }
      return state.user
    }
  },
  /*  store  */
  mutations: {
    /*  JSON  localStorage  `user`  */
    $_setStorage(state, value) {
      state.user = value
      localStorage.setItem(key, JSON.stringify(value))
      // console.log("stateUser", state.user)
    },
    /*  localStorage  */
    $_removeStorage(state) {
      state.user = null
      localStorage.removeItem(key)
    },
  },
  /*  mutations  */
  actions: {
    /*  _setStorage  */
    saveUserInfo({commit}, data) {
      commit('$_setStorage', data)
    },
    /*  _removeStorage  */
    clearUserInfo({commit}) {
      commit('$_removeStorage');
    },
  }
};

export default user